ckground:#1e1e1e;-webkit-box-orient:vertical;-webkit-box-direction:normal;-ms-flex-direction:column;flex-direction:column;width:-webkit-min-content;width:-moz-min-content;width:min-content;margin:10px auto;padding:20px;border-radius:10px}.login-form-wrapper .login-form .input-container{max-width:500px;margin-bottom:20px;width:35vmin}.login-form-wrapper .login-form input,.login-form-wrapper .login-form button{font-size:25px}@media (max-width: 650px){.login-form{width:100% !important}.login-form .input-container{width:80% !important}h1{font-size:25px}}
